}
h2,
h3 {
  font-size: 1.3125em;
  /*21 / 16*/
  line-height: 1.15;
  /*24 / 21*/
  margin-top: 2.285714em;
  /*48 / 21*/
  margin-bottom: 1.15em;
  /*24 / 21*/
}
h3 {
  font-weight: normal;
}
h4 {
  font-size: 1.125em;
  /*18 / 16*/
  margin-top: 2.67em;
  /*48 / 18*/
}
h5,
h6 {
  font-size: 1em;
  /*16*/
}
h1 {
  border-bottom: 1px solid;
  margin-bottom: 1.875em;
  padding-bottom: 0.8125em;
}
/* links */

a {
  text-decoration: none;
  color: #065588;
}
a:hover,
a:active {
  text-decoration: underline;
}
/* block spacing */

p,
blockquote,
.md-fences {
  margin-bottom: 1.5em;
}
h1,
h2,
h3,
h4,
h5,
h6 {
  margin-bottom: 1.5em;
}
/* blockquote */

blockquote {
  font-style: italic;
  border-left: 5px solid;
  margin-left: 2em;
  padding-left: 1em;
}
/* lists */

ul,
ol {
  margin: 0 0 1.5em 1.5em;
}
/* tables */
.md-meta,
.md-before,
.md-after {
  color: #999;
}

table {
  margin-bottom: 1.5em;
  /*24 / 16*/
  font-size: 1em;
  /* width: 100%; */
}
thead th,
tfoot th {
  padding: 0.25em 0.25em 0.25em 0.4em;
  text-transform: uppercase;
}
th {
  text-align: left;
}
td {
  vertical-align: top;
  padding: 0.25em 0.25em 0.25em 0.4em;
}

code,
.md-fences {
  background-color: #dadada;
}

code {
  padding-left: 2px;
  padding-right: 2px;
}

.md-fences {
  margin-left: 2em;
  margin-bottom: 3em;
  padding-left: 1ch;
  padding-right: 1ch;
}

pre,
code,
tt {
  font-size: 0.875em;
  line-height: 1.714285em;
}
/* some fixes */

h1 {
  line-height: 1.3em;
  font-weight: normal;
  margin-bottom: 0.5em;
}

p + ul,
p + ol {
  margin-top: 0.5em;
}

h3 + ul,
h4 + ul,
h5 + ul,
h6 + ul,
h3 + ol,
h4 + ol,
h5 + ol,
h6 + ol {
  margin-top: 0.5em;
}

li > ul,
li > ol {
  margin-top: inherit;
  margin-bottom: 0;
}

li ol > li {
  list-style-type: lower-alpha;
}

li li ol > li {
  list-style-type: lower-roman;
}

h2,
h3 {
  margin-bottom: 0.75em;
}
hr {
  border-top: none;
  border-right: none;
  border-bottom: 1px solid;
  border-left: none;
}
h1 {
  border-color: #c5c5c5;
}
blockquote {
  border-color: #bababa;
  color: #656565;
}

blockquote ul,
blockquote ol {
  margin-left: 0;
}

.ty-table-edit {
  background-color: transparent;
}
thead {
  background-color: #dadada;
}
tr:nth-child(even) {
  background: #e8e7e7;
}
hr {
  border-color: #c5c5c5;
}
.task-list {
  padding-left: 1rem;
}

.md-task-list-item {
  padding-left: 1.5rem;
  list-style-type: none;
}

.md-task-list-item > input:before {
  content: '\221A';
  display: inline-block;
  width: 1.25rem;
  height: 1.6rem;
  vertical-align: middle;
  text-align: center;
  color: #ddd;
  background-color: #f3f2ee;
}

.md-task-list-item > input:checked:before,
.md-task-list-item > input[checked]:before {
  color: inherit;
}

#write pre.md-meta-block {
  min-height: 1.875rem;
  color: #555;
  border: 0px;
  background: transparent;
  margin-top: -4px;
  margin-left: 1em;
  margin-top: 1em;
}

.md-image > .md-meta {
  color: #9b5146;
}

.md-image > .md-meta {
  font-family: Menlo, 'Ubuntu Mono', Consolas, 'Courier New', 'Microsoft Yahei',
    'Hiragino Sans GB', 'WenQuanYi Micro Hei', serif;
}

#write > h3.md-focus:before {
  left: -1.5rem;
  color: #999;
  border-color: #999;
}
#write > h4.md-focus:before {
  left: -1.5rem;
  top: 0.25rem;
  color: #999;
  border-color: #999;
}
#write > h5.md-focus:before {
  left: -1.5rem;
  top: 0.3125rem;
  color: #999;
  border-color: #999;
}
#write > h6.md-focus:before {
  left: -1.5rem;
  top: 0.3125rem;
  color: #999;
  border-color: #999;
}

.md-toc:focus .md-toc-content {
  margin-top: 19px;
}

.md-toc-content:empty:before {
  color: #065588;
}
.md-toc-item {
  color: #065588;
}
#write div.md-toc-tooltip {
  background-color: #f3f2ee;
}

#typora-sidebar {
  background-color: #f3f2ee;
  -webkit-box-shadow: 0 6px 12px rgba(0, 0, 0, 0.375);
  box-shadow: 0 6px 12px rgba(0, 0, 0, 0.375);
}

.pin-outline #typora-sidebar {
  background: inherit;
  box-shadow: none;
  border-right: 1px dashed;
}

.pin-outline #typora-sidebar:hover .outline-title-wrapper {
  border-left: 1px dashed;
}

.outline-item:hover {
  background-color: #dadada;
  border-left: 28px solid #dadada;
  border-right: 18px solid #dadada;
}

.typora-node .outline-item:hover {
  border-right: 28px solid #dadada;
}

.outline-expander:before {
  content: '\f0da';
  font-family: FontAwesome;
  font-size: 14px;
  top: 1px;
}

.outline-expander:hover:before,
.outline-item-open > .outline-item > .outline-expander:before {
  content: '\f0d7';
}

.modal-content {
  background-color: #f3f2ee;
}

.auto-suggest-container ul li {
  list-style-type: none;
}

/** UI for electron */

.megamenu-menu,
#top-titlebar,
#top-titlebar *,
.megamenu-content {
  background: #f3f2ee;
  color: #1f0909;
}

.megamenu-menu-header {
  border-bottom: 1px dashed #202b33;
}

.megamenu-menu {
